import React, { Suspense, useRef, useEffect, lazy } from "react";
import { PostTrimContent, themeImpressionTracking } from '../../../../utils'
import { isTagEmbed, isTagShop } from "../../../../actions/api";
const AuthorDetails = lazy(() => import('./AuthorDetails'));
const Media = lazy(() => import('./Media'));
const ShopButton = lazy(() => import('../../Elements/ShopButton/ShopButton'));
const Network = lazy(() => import('../../Elements/Network/Network'));
const PostContent = lazy(() => import('../../Elements/PostContent/PostContent'));

const trim_content_length = !isTagEmbed ? 200 : null;
const Card = ({ itemData, personalization, ThemeRule, clickToShowPopUp, itemIndex, wallID, ownerId, postSizeInPx, wall, onClickPopUpSlider }) => {
  const postTrack = useRef(null);
  const mediaType = itemData.type !== 1 ? true : false;
  const postTextContent = personalization.trimcontent ? PostTrimContent(itemData.content, trim_content_length) : itemData.content
  const responsiveCardSize2x = postSizeInPx.adjustWidth < 250 ? true : false
  const themeMode = (ThemeRule.postHover == 0 && isTagShop) ? 'light' : 'dark'
  const isHotspot = itemData.hotspot && itemData.ugc_products.length > 0 ? true : false
  const isShopBtn = isTagShop && ThemeRule.shoppingText != '' && (!itemData.hotspot && itemData.ugc_products.length > 0) ? true : false;
  useEffect(() => {
    if (isTagShop) themeImpressionTracking(postTrack);
  }, []);
  useEffect(() => {
    if (itemData.isPopUp) onClickPopUpSlider(itemIndex, itemData)
  }, [itemIndex, itemData.isPopUp, itemData]);
  return (
    <div id={`tb-hs-post-${itemData.id}`} className={`tb_gt_post_wrapper${responsiveCardSize2x ? ' tb_gt_post_wrapper2x' : ''}`} tb-network={itemData.network.id} tb-product={isShopBtn ? 1 : isHotspot ? 2 : 0} ref={postTrack} post-id={itemData.referenceId ? itemData.referenceId : itemData.id} feed-id={itemData.feedId}>

      <div className={`tb_gt_post_in${isTagShop ? ' tb_gt_post_ani' : ''}`} onClick={clickToShowPopUp(itemIndex, itemData)} role="button" aria-roledescription="Click to open pop up" tabIndex={"0"}>
        <div className="tb_gt_post_media_wrapp" role="button" tabIndex="0" aria-label="Pop Up Button">
          {mediaType ? <Suspense fallback={<></>}><Media itemData={itemData} wallID={wallID} wall={wall} ownerId={ownerId} /></Suspense> : ''}
          {!isHotspot ? <>
            <div className="tb_gt_post_details">
              {isTagShop ? <div className="tb_gt_social_">
                <Suspense fallback={<></>}><Network networkClass={'tb_gt_social__ico'} network={itemData.network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} Personalization={personalization} /></Suspense>
              </div> : null}
              {isShopBtn ? <Suspense fallback={<></>}><ShopButton ThemeRule={ThemeRule} btnClass={'tb_gt_btn_wrap'} /></Suspense> : null}
              {!isTagShop ? <>
                <div className="tb_gt_contant_wrapper">
                  {!ThemeRule.hideContent || itemData.type == 1 ? <Suspense fallback={<></>}><PostContent contentClass={'tb_gt_content'} item={itemData} content={postTextContent} font={ThemeRule} ThemeRule={ThemeRule} personalization={personalization} contentTitle={itemData.contentTitle} /></Suspense> : ''}
                </div>
                <Suspense fallback={<></>}>
                <AuthorDetails ownerId={ownerId} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} />
                </Suspense>
              </>
                : null}
            </div>
            <div className={`tb_gt_post_overlay tb_gt_post_overlay-${themeMode}`}><div></div></div>
          </> : null}
        </div>
      </div>
    </div>
  );
}
export default Card